body {
  padding: 10px;
}
.App {
  text-align: center;
}

.FranceSvg path {
  stroke-width: 1px;
  fill: #fff;
  stroke: black;
}

.departments-list {
  margin-top: 16px;
  display: flex;
  flex-wrap: wrap;
  row-gap: 5px;
}

.departments-list__item {
  flex-basis: 25%;
  margin: 0;
  font-size: 12px;
  cursor: pointer;
}

.departments-list__item:hover {
  font-weight: 900;
}


path {
  cursor: pointer;
}
path:hover {
  stroke-width: 2px;
}

.france-wrapper {
  display: flex;
  gap: 20px;
  padding: 10px 0 0;

}

.caption-color {
  width: 15px;
  height: 15px;
  border-radius: 2px;
  margin-right: 8px;
  display: inline-block;
}